  /* hover color */
  --hover-red-color: #f4908b;
  --hover-bg-color: #451717;
  --hover-select-bg-color: #302626;

  /* border */
  --main-border: 30px solid var(--black-color);
  --item-border: 2px solid var(--border-red-color);
  --item-headline-border: 1px solid var(--border-red-color);
  --edit-border: 2px solid var(--border-yelow-color);
  --edit-headline-border: 1px solid var(--border-yelow-color);
  --radius-n: 3px;
  --radius-small: 2px;

